.chart-container {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  /*Indicators*/ }
  .chart-container .graph-focus-margin {
    margin: 0px 5%; }
  .chart-container > .title {
    margin-top: 25px;
    margin-left: 25px;
    text-align: left;
    font-weight: normal;
    font-size: 12px;
    color: #6c7680; }
  .chart-container .graphics {
    margin-top: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    position: relative; }
  .chart-container .graph-stats-group {
    display: flex;
    justify-content: space-around;
    flex: 1; }
  .chart-container .graph-stats-container {
    display: flex;
    justify-content: space-around;
    padding-top: 10px; }
    .chart-container .graph-stats-container .stats {
      padding-bottom: 15px; }
    .chart-container .graph-stats-container .stats-title {
      color: #8D99A6; }
    .chart-container .graph-stats-container .stats-value {
      font-size: 20px;
      font-weight: 300; }
    .chart-container .graph-stats-container .stats-description {
      font-size: 12px;
      color: #8D99A6; }
    .chart-container .graph-stats-container .graph-data .stats-value {
      color: #98d85b; }
  .chart-container .axis, .chart-container .chart-label {
    font-size: 11px;
    fill: #555b51; }
    .chart-container .axis line, .chart-container .chart-label line {
      stroke: #dadada; }
  .chart-container .percentage-graph .progress {
    margin-bottom: 0px; }
  .chart-container .data-points circle {
    stroke: #fff;
    stroke-width: 2; }
  .chart-container .path-group path {
    fill: none;
    stroke-opacity: 1;
    stroke-width: 2px; }
  .chart-container line.dashed {
    stroke-dasharray: 5,3; }
  .chart-container .tick.x-axis-label {
    display: block; }
  .chart-container .tick .specific-value {
    text-anchor: start; }
  .chart-container .tick .y-value-text {
    text-anchor: end; }
  .chart-container .tick .x-value-text {
    text-anchor: middle; }
  .chart-container .progress {
    height: 20px;
    margin-bottom: 20px;
    overflow: hidden;
    background-color: #f5f5f5;
    border-radius: 4px;
    -webkit-box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
    box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1); }
  .chart-container .progress-bar {
    float: left;
    width: 0;
    height: 100%;
    font-size: 12px;
    line-height: 20px;
    color: #fff;
    text-align: center;
    background-color: #36414c;
    -webkit-box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.15);
    box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.15);
    -webkit-transition: width .6s ease;
    -o-transition: width .6s ease;
    transition: width .6s ease; }
  .chart-container .graph-svg-tip {
    position: absolute;
    z-index: 99999;
    padding: 10px;
    font-size: 12px;
    color: #959da5;
    text-align: center;
    background: rgba(0, 0, 0, 0.8);
    border-radius: 3px; }
    .chart-container .graph-svg-tip ul {
      padding-left: 0;
      display: flex; }
    .chart-container .graph-svg-tip ol {
      padding-left: 0;
      display: flex; }
    .chart-container .graph-svg-tip ul.data-point-list li {
      min-width: 90px;
      flex: 1;
      font-weight: 600; }
    .chart-container .graph-svg-tip strong {
      color: #dfe2e5;
      font-weight: 600; }
    .chart-container .graph-svg-tip .svg-pointer {
      position: absolute;
      height: 5px;
      margin: 0 0 0 -5px;
      content: " ";
      border: 5px solid transparent;
      border-top-color: rgba(0, 0, 0, 0.8); }
    .chart-container .graph-svg-tip.comparison {
      padding: 0;
      text-align: left;
      pointer-events: none; }
      .chart-container .graph-svg-tip.comparison .title {
        display: block;
        padding: 10px;
        margin: 0;
        font-weight: 600;
        line-height: 1;
        pointer-events: none; }
      .chart-container .graph-svg-tip.comparison ul {
        margin: 0;
        white-space: nowrap;
        list-style: none; }
      .chart-container .graph-svg-tip.comparison li {
        display: inline-block;
        padding: 5px 10px; }
  .chart-container .indicator,
  .chart-container .indicator-right {
    background: none;
    font-size: 12px;
    vertical-align: middle;
    font-weight: bold;
    color: #6c7680; }
  .chart-container .indicator i {
    content: '';
    display: inline-block;
    height: 8px;
    width: 8px;
    border-radius: 8px; }
  .chart-container .indicator::before, .chart-container .indicator i {
    margin: 0 4px 0 0px; }
  .chart-container .indicator-right::after {
    margin: 0 0 0 4px; }
